import React, { useState } from "react";
import ReactTooltip from "react-tooltip";
import { jsPDF } from "jspdf";




export default function TextForm(props) {
  const [text, setText] = useState("");
  const handleUpClick = () => {
    let newText = text.toUpperCase();
    setText(newText);
    props.showAlert("Converted to uppercase!", "success");
  };
  const handleLowClick = () => {
    let newText = text.toLowerCase();
    setText(newText);
    props.showAlert("Converted to lowercase!", "success");
  };
  const handleCopy = () => {
    navigator.clipboard.writeText(text);
    props.showAlert("Copied to clipbord!", "success");
  };
  const handleExtraSpaces = () => {
    let newText = text.split(/[ ]+/);
    setText(newText.join(" "));
    props.showAlert("Extra spaces Removed!", "success");
  };
  const handleCapitalize = () => {
    let newText = text
      .split(" ")
      .map((el) => el.charAt(0).toUpperCase() + el.slice(1))
      .join(" ");
    setText(newText);
    props.showAlert("Converted to Capitalize!", "success");
  };
  const handleSelect = () => {
    var text = document.getElementById("myBox");
    text.select();
  };

  const handleSenClick = () => {
    let newText = text.toLowerCase().replace(/(^\s*\w)/g, function(c) {
      return c.toUpperCase();
    });
    setText(newText);
    props.showAlert("Converted to sentenceCase!", "success");
  };

  const replaceString = () => {
    let repval = prompt("Enter word to be replace : ");
    let tobereplaced = new RegExp(repval, "g");

    let toreplace = prompt("Enter word you want to replace with : ");

    let newText = text.replace(tobereplaced, toreplace);
    setText(newText);
    props.showAlert("word is Replaced !", "success");
  };

  function handleDuplicates() {
    let wordArr = text.split(" ");
    let newText = wordArr.filter((item, pos) => {
      return wordArr.indexOf(item) === pos;
    });
    newText = newText.join(" ");
    setText(newText);
    props.showAlert("Dublicate word Removed!", "success");
  }

  const [mailIds, setMail_Ids] = useState("");
  const handleMailExtractor = () => {
    const arr = text.split(" ");
    arr.forEach((elements) => {
      if (elements.includes("@") && elements.includes(".")) {
        setMail_Ids(mailIds + elements + " ");
      }
      props.showAlert("Extrated Mail Below!", "success");
    });
  };
  const handleDummy = (e) => {
    setText(
      "Lorem ipsum dolor sit, amet consectetur adipisicing elit. Porro, deleniti facere vel quis cupiditate molestias quae! Aliquid dolorum architecto iure. Similique ipsum repellendus numquam quae ducimus quibusdam aut voluptatum cupiditate?, Lorem ipsum dolor sit, amet consectetur adipisicing elit. Porro, deleniti facere vel quis cupiditate molestias quae! Aliquid dolorum architecto iure. Similique ipsum repellendus numquam quae ducimus quibusdam aut voluptatum cupiditate? Lorem ipsum dolor sit, amet consectetur adipisicing elit. Porro, deleniti facere vel quis cupiditate molestias quae! Aliquid dolorum architecto iure. Similique ipsum repellendus numquam quae ducimus quibusdam aut voluptatum cupiditate?"
    );
    props.showAlert("Generate Sample Text!", "success");
  };

  const downloadTxtFile = () => {
    const element = document.createElement("a");
    const file = new Blob([text], {
      type: "text/plain",
      fontStyle :"Regular",
    });
    element.href = URL.createObjectURL(file);
    element.download = "MyTextFile.txt";
    props.showAlert("Text File Downloaded!", "success");

    element.click();
  };

   const downloadDocFile = () => {
    const element = document.createElement("a");

   let file = new Blob([text], {
      type: "text/plain",
    });
    element.href = URL.createObjectURL(file);
    element.download = "MyDocFile.doc";
    element.click();
    props.showAlert("Word File Downloaded!", "success");
  };


  const downloadPdf = () => {
    const doc = new jsPDF("p", "pt");
    const pdfText = text;
    doc.text(20, 100, pdfText, { maxWidth: 540 });
    doc.setFont("karla");
    doc.setFontSize("1.5rem");
    doc.save("MyPdfFile.pdf");
    props.showAlert("PDF File Downloaded!", "success");
  };

  const shareWa = (e) => {
    const url = "whatsapp://send?text=" + text;
    e.preventDefault();
    window.location.href = url;
  };
  const speak = () => {
    let msg = new SpeechSynthesisUtterance();
    msg.text = text;
    window.speechSynthesis.speak(msg);
    props.showAlert("Listen Your Text!", "success");
  };
  const handleSaveClick = (e) => {
    localStorage.setItem("Text", text);
    props.showAlert("Save on textBord", "success");
  };
  const handleRetrieve = (e) => {
    let a = localStorage.getItem("Text");
    setText(a);
  };
  const readTxt = (event) => {
    let file = event.target.files[0];
    let reader = new FileReader();
    reader.onload = function(event) {
      setText(event.target.result);
    };
    reader.readAsText(file);
    props.showAlert("Text File Uploaded", "success");
  };

  const handleClearClick = () => {
    let newText = "";
    setText(newText);
    props.showAlert("Text is cleared", "success");
  };
  const handleOnchange = (event) => {
    setText(event.target.value);
  };

  return (
    <>
      <div
        className="mb-3"
        style={{ color: props.mode === "dark" ? "white" : "black" }}
      >
        <h2 className="mb-2">{props.heading}</h2>
      </div>
      <div className="mb-3">
        <textarea
          className="form-control"
          id="myBox"
          rows="8"
          onChange={handleOnchange}
          onFocus={handleSelect}
          value={text}
          autoFocus
          style={{
            backgroundColor: props.mode === "dark" ? "#343b41" : "white",
            color: props.mode === "dark" ? "white" : "black",
          }}
          placeholder="Enter Your Text here"
        ></textarea>
        <div
          className="mb-3 my-2"
          style={{ color: props.mode === "dark" ? "white" : "black" }}
        >
          <h2 className="mb-3">Text Analysis and Summarization</h2>

          <p>
            <b>
              {" "}
              {
                text.split(/\S+/).filter((element) => {
                  return element.length !== 0;
                }).length
              }
            </b>{" "}
            words and <b> {text.replace(/\s+/g, "").length} </b>characters
          </p>
          <p>
            <b>
              {0.08 *
                text.split(/\S+/).filter((element) => {
                  return element.length !== 0;
                }).length}
            </b>{" "}
            Minutes to Read
          </p>
          <p>
            <b>
              {
                text.split(/\S+\r\n|\r|\n/).filter((element) => {
                  return element.length !== 0;
                }).length
              }
            </b>{" "}
            Paraghraphs
          </p>
          <p>
            <b>{text.replace(/ /g, "").length / text.split(" ").length}</b>{" "}
            Characters per word
          </p>
          <p>
            {" "}
            Email IDs :{" "}
            <b> {mailIds.length > 0 ? mailIds : "No Mail is here !"} </b>{" "}
          </p>

          <label
            htmlFor="formFilesm"
            className="form-label"
          >
            Upload File For Text
          </label>
          <input
            className="form-control form-control-sm"
            onChange={readTxt}
            type="file"
            id="formFileSm"
            style={{
              backgroundColor: props.mode === "dark" ? "#343b41" : "white",
              color: props.mode === "dark" ? "white" : "black",
            }}
          />
        </div>
        <button
          disabled={text.length === 0}
          className="btn btn-primary my-2 mx-1"
          onClick={handleUpClick}
          data-tip="Uppercase Your TextE"
        >
          Uppercase
        </button>
        <button
          disabled={text.length === 0}
          className="btn btn-primary my-2 mx-1"
          onClick={handleLowClick}
          data-tip="Lowercase Your Text"
        >
          Lowercase
        </button>
        <button
          disabled={text.length === 0}
          className="btn btn-primary my-2 mx-1"
          onClick={handleExtraSpaces}
          data-tip="Remove  Extra Spaces"
        >
          Remove spaces
        </button>
        <button
          disabled={text.length === 0}
          className="btn btn-primary my-2 mx-1"
          onClick={handleCapitalize}
          data-tip="Capitalize Your Text"
        >
          Capitalize
        </button>
        <button
          disabled={text.length === 0}
          className="btn btn-primary my-2 mx-1"
          onClick={replaceString}
          data-tip="Replace Word From Your text"
        >
          Replace Word
        </button>
        <button
          disabled={text.length === 0}
          className="btn btn-primary my-2 mx-1"
          onClick={handleDuplicates}
          data-tip="Remove Your Dublicate Words"
        >
          Remove Duplicates
        </button>
        <button
          disabled={text.length === 0}
          className="btn btn-primary my-2 mx-1"
          onClick={handleSenClick}
          data-tip="Sentence Your Text"
        >
          Sentence Case
        </button>
        <button
          disabled={text.length === 0}
          className="btn btn-primary my-2 mx-1"
          onClick={handleMailExtractor}
          data-tip="Extract Email"
        >
          Mail Extract
        </button>
        <button
          className="btn btn-primary my-2 mx-1"
          onClick={handleDummy}
          data-tip="Create Sample Text"
        >
          Dummy Text
        </button>

        <div className="btn-group">
          <button
            disabled={text.length === 0}
            type="button"
            className="btn btn-primary my-2 "
            onClick={downloadTxtFile}
            data-tip="Download Text File"
          >
            {" "}
            <i className="bi bi-file-earmark-arrow-down"></i> Downlod File
          </button>
          <button
            disabled={text.length === 0}
            type="button"
            className="btn btn-primary dropdown-toggle dropdown-toggle-split my-2"
            data-tip="More Option For Dowload File"
            data-bs-toggle="dropdown"
            aria-expanded="false"
          ></button>
          <ul className="dropdown-menu" style={{ cursor: "pointer" }}>
            <li>
              <a className="dropdown-item" onClick={downloadDocFile} href="/">
                {" "}
                <i className="bi bi-file-earmark-word"></i> DOCS
              </a>
            </li>
            <li>
              <a className="dropdown-item" onClick={downloadPdf} href="/">
                {" "}
                <i className="bi bi-file-earmark-ppt"></i> PDF
              </a>
            </li>
           
            <li>
             
            </li>
            <li>
              <a className="dropdown-item" onClick={downloadTxtFile} href="/">
                {" "}
                <i className="bi bi-file-earmark-font"></i> TEXT
              </a>
            </li>
          </ul>
        </div>
        <br />

        <button
          disabled={text.length === 0}
          type="submit"
          onClick={handleSaveClick}
          className="btn btn-secondary mx-1 "
          data-tip="Save Text On textbord "
        >
          <i className="bi bi-cloud-download"></i>
        </button>
        <button
          disabled={text.length === 0}
          type="submit"
          onClick={handleRetrieve}
          className="btn btn-secondary my-2 "
          data-tip="Word That is not saved will be Removed"
        >
          <i className="bi bi-backspace"></i>
        </button>
        <button
          disabled={text.length === 0}
          type="submit"
          onClick={speak}
          className="btn btn-secondary  mx-1"
          data-tip="Listen to the entered word"
        >
          <i className="bi bi-volume-up-fill"></i>
        </button>
        <button
          disabled={text.length === 0}
          type="submit"
          onClick={shareWa}
          className="btn btn-secondary mx-1"
          data-tip="Share Text on Whatsapp"
        >
          <i class="bi bi-whatsapp"></i>
        </button>

        <button
          disabled={text.length === 0}
          className="btn btn-secondary  mx-1"
          onClick={handleCopy}
          data-tip="copy Text"
        >
          Copy
        </button>
        <button
          disabled={text.length === 0}
          className="btn btn-danger  mx-1"
          onClick={handleClearClick}
          data-tip="Clear Text Box"
        >
          Clear
        </button>
      </div>
      <ReactTooltip />
      <div
        className="container"
        style={{ color: props.mode === "dark" ? "white" : "black" }}
      >
        <h3>Preview</h3>
        <p> {text.length > 0 ? text : "Nothing to preview"} </p>
      </div>
    </>
  );
}
