
import './App.css';
import Navbar from './componets/Navbar';
import TextForm from './componets/TextForm';
import React, { useState } from 'react';
import Alert from './componets/Alert';
import About from './componets/About';
import {
  BrowserRouter as Router,
  Switch,
  Route
} from "react-router-dom";

function App() {
  const [mode, setMode] = useState('light');
  const [alert, setAlert] = useState(null);
   const showAlert = (message, type)=>{
    setAlert({
      msg:message,
      type : type
    })
    setTimeout(() => {
      setAlert(null);
    }, 1500);
   }
  const toggleMode = ()=>{
    if(mode === 'light') {
      setMode ('dark' )
      document.body.style.backgroundColor ='#343b41';
      showAlert("Dark Mode is Activated", "success :");
    } 
    else{
      setMode ('light' )
      document.body.style.backgroundColor ='white';
      showAlert("Dark Mode is Deactivated", "success :");  
    }
    
  } 
   return (
   <>
<Router>

<Navbar title="Text Utilies" aboutText="About" mode={mode} toggleMode={toggleMode} />
<Alert Alert={alert}/>
    <div className="container my-3">
          
    <Switch>
           <Route exact path="/about"> 
            <About mode={mode} />
          
          </Route>
          <Route  exact path="/">
           
<TextForm showAlert={showAlert} heading="Unlock the Power of Textutils : Access Word and Character Counting Tools" mode={mode}/>
          </Route>
        </Switch>


    </div>
</Router>

   </>
  );
}

export default App;
